<template>
	<div>
		<div class="data-panel">
			<div class="data-num">
				<ul>
					<li>54925330</li>
					<li>424170</li>
				</ul>
			</div>
			<div class="data-desc">
				<ul>
					<li>数据量</li>
					<li>人数</li>
				</ul>
			</div>
		</div>
		<div class="map">
			<div class="map1"></div>
			<div class="map2"></div>
			<div class="map3"></div>
			<div ref="mapChart" class="chart"></div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'MainPanel',
		data() {
			return {
				list: []
			}
		},
		methods: {
			drawMap(map) {
				let chart = this.$echarts.getInstanceByDom(this.$refs.mapChart);
				if (!chart) {
					chart = this.$echarts.init(this.$refs.mapChart);
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(chart)
					})
				}
				if (!this.$echarts.getMap('China')) {
					this.$echarts.registerMap('China', map)
				}
				let mapData = map.features.map(function(item) {
					return {
						name: item.properties.name,
						value: item.properties.childNum,
						cp: item.properties.cp,
					}
				});
				let convertData = function(areaValue) {
					let res = [];
					for (let i = 0; i < areaValue.length; i++) {
						mapData.forEach((v) => {
							if (v.name == areaValue[i].name) {
								res.push({
									name: areaValue[i].name,
									value: v.cp.concat(areaValue[i].value)
								});
							}
						});
					}
					return res;
				};
				let option = {
					tooltip: {
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					geo: {
						map: 'China',
						roam: true,
						zoom: 1.2,
						itemStyle: {
							areaColor: "rgba(20, 41, 87,0.6)",
							borderColor: "#195BB9",
							borderWidth: 1
						},
						emphasis: {
							itemStyle: {
								areaColor: "#2B91B7"
							},
							label: {
								show: false
							}
						}
					},
					series: [{
							name: '销量',
							type: 'scatter',
							coordinateSystem: 'geo',
							symbolSize: 10,
							encode: {
								value: 2
							},
							data: convertData(this.list).slice(3)
						},
						{
							name: '销量第一',
							type: 'effectScatter',
							coordinateSystem: 'geo',
							symbolSize: 30,
							color: "gold",
							encode: {
								tooltip: 2
							},
							data: convertData(this.list).slice(0, 1)
						},
						{
							name: '销量第二',
							type: 'effectScatter',
							coordinateSystem: 'geo',
							symbolSize: 25,
							color: "silver",
							encode: {
								tooltip: 2
							},
							data: convertData(this.list).slice(1, 2)
						},
						{
							name: '销量第三',
							type: 'effectScatter',
							coordinateSystem: 'geo',
							symbolSize: 20,
							color: "#B5A642",
							encode: {
								tooltip: 2
							},
							data: convertData(this.list).slice(2, 3)
						}
					]
				};
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'expend/province.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					let name = resp.data[i][0];
					let value = resp.data[i][1];
					this.list.push({
						name: name,
						value: value
					})
				}
				this.$axios.get(process.env.BASE_URL + 'map/china.json').then(resp => {
					this.drawMap(resp.data);
				})
			})
		}
	}
</script>

<style>
	.data-panel {
		background: rgba(101, 132, 226, 0.1);
		padding: 10px;
	}

	.data-panel li {
		list-style: none;
		animation: show 1s ease-in;
	}

	.data-panel .data-num {
		border: 1px solid rgba(25, 186, 139, 0.17);
		position: relative;
	}

	.data-panel .data-num ul {
		display: flex;
	}

	.data-panel .data-num ul li {
		flex: 1;
		line-height: 60px;
		font-size: 56px;
		color: #ffeb7b;
		text-align: center;
		font-family: "electronicFont";
		position: relative;
	}

	.data-panel .data-num ul li::after {
		position: absolute;
		content: "";
		top: 25%;
		right: 0;
		height: 50%;
		width: 0.0125rem;
		background: rgba(255, 255, 255, 0.2);
	}

	.data-panel .data-num::before {
		position: absolute;
		top: 0;
		left: 0;
		content: "";
		width: 30px;
		height: 15px;
		border-top: 2px solid #02A6B5;
		border-left: 2px solid #02A6B5;
	}

	.data-panel .data-num::after {
		position: absolute;
		bottom: 0;
		right: 0;
		content: "";
		width: 30px;
		height: 15px;
		border-bottom: 2px solid #02A6B5;
		border-right: 2px solid #02A6B5;
	}

	.data-panel .data-desc ul {
		display: flex;
	}

	.data-panel .data-desc ul li {
		flex: 1;
		text-align: center;
		color: rgba(255, 255, 255, 0.7);
		font-size: 16px;
		height: 30px;
		line-height: 30px;
		padding-top: 4px;
	}

	.map {
		height: 80%;
		position: relative;
		animation: show 2s ease-in;
	}

	.map .map1 {
		width: 425px;
		height: 425px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background: url(../../assets/expand/map.png);
		opacity: 0.3;
		background-size: 100% 100%;
		z-index: 0;
	}

	.map .map2 {
		width: 520px;
		height: 520px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background: url(../../assets/expand/lbx.png);
		opacity: 0.6;
		background-size: 100% 100%;
		animation: rotate2 15s linear infinite;
	}

	.map .map3 {
		width: 460px;
		height: 460px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background: url(../../assets/expand/jt.png);
		opacity: 0.6;
		background-size: 100% 100%;
		animation: rotate3 10s linear infinite;
	}

	@keyframes show {
		from {
			opacity: 0;
		}
	}


	.map .chart {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}

	@keyframes rotate2 {
		from {
			transform: translate(-50%, -50%) rotate(0deg);
		}

		to {
			transform: translate(-50%, -50%) rotate(360deg);
		}
	}

	@keyframes rotate3 {
		from {
			transform: translate(-50%, -50%) rotate(0deg);
		}

		to {
			transform: translate(-50%, -50%) rotate(-360deg);
		}
	}
</style>
